@import "./fonts/fonts.css";
@import "./abstracts/_mixins.scss";
@import "./abstracts/variables.scss";

$font2: "Corsica";
$font1: "Circular";
$font3: "HK-Grotesk";
$lightblack: rgb(54, 60, 76);
$lightgrey1: #d1d9dc;
$lightgreen: #bcd4bd;
$lightbrown: #e6e2c7;
$lightpurple: #d0cec9;

.facts {
  .section1, .section2 {
    // margin: 20px;
    display: flex;
    justify-content: space-evenly;

  }
  .f1, .f2, .f3, .f4 {
    text-align: center;
    border-radius: 8px;
    height: 200px;
    width: 200px;
    margin: 10px;
    align-items: center;
    justify-content: center;
    
    @include sm {
      width: 150px;
      height: 150px;
    }
    
    p {
      font-size: 3rem;
      color: black;
      margin: 10px;
      
      @include sm {
        font-size: 2rem;
      }
    }
  }
  .f1 {
    background:$lightgrey1;
  }
  .f2 {
    background: #bcd4bd;
  }
  
  .f3 {
    background:$lightbrown;
  }
  .f4 {
    background: $lightpurple;
  }
}

.facts {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  font-family: $font1;
  padding-bottom: 10%;

  @include md {
    flex-direction: column;
  }

  .Milk {
    margin-left: 20%;
    margin-top: 12%;
    width: 15%;
    height: 15%;

    @include lg {
      display: none;
    }

    @include xl {
      display: none;
    }
    
    @include sm {
      display: none;
    }
    @include md {
      display: none;
    }
  }

  .download-btn {
    cursor: pointer;
    padding: 0 30px;
    border-radius: 4px;
    height: 50px;
    background: none;

    font-family: $font2;
    text-transform: uppercase;
    letter-spacing: 5px;
    color: $lightblack;
    border: 2px solid $lightblack;
    box-shadow: 1px solid rgb(94, 94, 94);
    margin: 10px;
    
    @include sm {
      font-size: 12px;
    }
  }

  .cards {
    display: flex;
    flex-direction: column;
    padding: 20px;
    font-size: 1.3rem;
  }

  .section1 {
    display: flex;
    flex-direction: row;
    width: 100%;
  }
  h1 {
    text-align: center;
    font-size:  1.8rem;
    margin-top: 40px;

    @include md {
      font-size: 2.8vw;
    }
    @include sm {
      margin-top: 20%;
      font-size: 1rem;
    }
  }

}
